@import '../../colors.scss';

.nav-bar {
	background: #181818;
	width: 70px;
	height: 100%;
	position: absolute;
	top: 0;
	z-index: 3;
	min-height: 500px;

	.logo {
		display: block;
		padding: 4px 0;

		img {
			display: block;
			margin: 2px auto;
			width: 50px;
			height: auto;

			&.sub-logo {
				width: 70px;
			}
		}
	}

	nav {
		display: block;
		text-align: center;
		position: absolute;
		height: 210px;
		top: 50%;
		margin-top: -120px;
		width: 100%;

		a {
			font-size: 22px;
			color: #4d4d4e;
			display: block;
			line-height: 51px;
			height: 51px;
			position: relative;
			text-decoration: none;

			i {
				transition: all 0.4s ease-out;
			}

			&:hover {
				color: $primary-color;

				svg {
					opacity: 0;
				}

				&:after {
					opacity: 1;
				}
			}

			&:after {
				content: '';
				font-size: 9px;
				letter-spacing: 2px;
				position: absolute;
				bottom: 0;
				display: block;
				width: 100%;
				text-align: center;
				opacity: 0;
				transition: all 0.4s ease-out;
			}

			&:first-child {
				&::after {
					content: 'HOME';
				}
			}
		}

		a.about-link {
			&:after {
				content: 'ABOUT';
			}
		}

		a.contact-link {
			&:after {
				content: 'CONTACT';
			}
		}

		a.portfolio-link {
			&:after {
				content: 'PORTFOLIO';
			}
		}

		a.active {
			svg {
				color: $primary-color;
			}
		}
	}

	ul {
		position: absolute;
		bottom: 20px;
		width: 100%;
		display: block;
		padding: 0;
		list-style: none;
		text-align: center;
		margin: 0;

		li {
			a {
				padding: 7px 0;
				display: block;
				font-size: 20px;
				line-height: 20px;

				&:hover svg {
					color: $primary-color;
				}
			}
		}
	}
}

.barsIcon,
.closeIcon {
	display: none;
}

@media screen and (max-width: 1200px) {
	.nav-bar {
		background: transparent;
		position: initial;
		height: auto;
		min-height: auto;

		ul,
		nav {
			display: none;
		}

		nav {
			width: 100%;
			height: 100%;
			background: #181818;
			top: 0;
			left: 0;
			position: fixed;
			z-index: 2;
			margin: 0;

			a {
				display: flex;
				justify-content: center;
				align-items: center;
			}

			a:after {
				opacity: 1;
				position: initial;
				width: auto;
				margin-left: 10px;
			}

			a svg {
				opacity: 1;
			}

			&.mobile-show {
				display: block;
			}
		}

		ul {
			&.mobile-show {
				display: block;
				z-index: 2;
				bottom: 40px;
			}

			li {
				margin: 10px auto;
			}
		}

		.barsIcon,
		.closeIcon {
			display: block;
			position: absolute;
			top: 12px;
			right: 15px;
		}
	}
}
