@import '../../colors.scss';

.home-page {
	.text-zone {
		position: absolute;
		left: 10%;
		top: 50%;
		transform: translateY(-50%);
		width: 100%;
		max-height: 90%;
	}

	h1 {
		color: $primary-text-color;
		font-size: 53px;
		margin: 0;
		font-family: 'Coolvetica', Verdana, Geneva, Tahoma, sans-serif;
		font-weight: 400;

		&::before {
			content: '<h1>';
			font-family: 'La Belle Aurore', Verdana, Geneva, Tahoma, sans-serif;
			color: $primary-color;
			font-size: 18px;
			position: absolute;
			margin-top: -40px;
			left: 15px;
			opacity: 0.6;
		}

		&::after {
			content: '<h1/>';
			font-family: 'La Belle Aurore', Verdana, Geneva, Tahoma, sans-serif;
			color: $primary-color;
			font-size: 18px;
			position: absolute;
			margin-top: 18px;
			margin-left: 20px;
			animation: fadeIn 1s 1.7s backwards;
			opacity: 0.6;
		}

		img {
			width: 32px;
		}
	}

	h2 {
		color: $primary-color;
		margin-top: 20px;
		font-weight: 400;
		font-size: 13px;
		letter-spacing: 3px;
		animation: fadeIn 1s 1.8s backwards;
	}

	.flat-button {
		color: $primary-color;
		font-size: 13px;
		font-weight: 400;
		letter-spacing: 4px;
		font-family: sans-serif;
		text-decoration: none;
		padding: 10px 18px;
		border: 1px solid $primary-color;
		margin-top: 25px;
		float: left;
		animation: fadeIn 1s 1.8s backwards;
		white-space: nowrap;

		&:hover {
			background: $primary-color;
			color: #333;
		}
	}
}

@media screen and (max-width: 1200px) {
	.tags,
	.home-page h1::before,
	.home-page h1::after {
		display: none;
	}

	.home-page {
		.text-zone {
			position: initial;
			width: 100%;
			transform: none;
			padding: 10px 28px;
			box-sizing: border-box;
		}

		.flat-button {
			float: none;
			display: block;
			margin: 20px auto 20px auto;
			width: auto;
			text-align: center;
		}
	}
}
